/* ############### */

.nav{
  display: flex;
  justify-content: space-between;
  margin: 15px 15px 20px 15px;
  align-items: baseline;
}

.btnCreateAccount{
  font-size: 11px !important;
  font-weight: 600 !important;
  background: #00C62F !important;
  border-radius: 10px !important;
  text-transform: full-size-kana !important;
  padding: 5px 14px !important;
}

.counter{
  display: flex;
  gap: 10px;
}

.counter p{
  font-weight: bold;
}

.chip{
  display: flex;
  background: #00C62F;
  color: white;
  font-weight: bold;
  border-radius: 25px;
  height: 30px;
  width: 30px;
  align-items: center;
  justify-content: center;
  margin-top: -8px;
}

.information{
  background: #D9FFE2;
  width: 80%;
  margin-top: 20px;
  padding: 20px;
}

.headInfo{
  display: flex;
  justify-content: space-between;
}

.headInfo span{
  font-weight: bold;
  font-size: 17px;
}

.headInfo span .icon-icoBankPlus {
  font-weight: normal !important;
  font-size: 16px !important;
}

.headInfo div{
  display: flex;
  align-items: center;
  gap: 0px;
}

.container{
  display: flex;
  justify-content: center;
}

.bankInfo{
  display: flex;
  background: white;
  width: 100%;
  height: 32px;
  align-items: center;
  gap: 20px;
  font-size: 19px;
  padding:25px 10px 25px 10px;
  border-radius: 10px;
}

@media all and (min-width: 718px){
 .itemSearch{
    width: 400px;
  }
}

@media all and (max-width: 717px){
  .nav{
    flex-direction: column; 
  }

  .counter{
    order: 1;
  }

  .itemSearch{
    order: 3;
    width: 100%;
  }

  .itemButton{
    order: 2;
    align-self: end;
    margin-top: -32px;
    margin-bottom: 8px;
  }
}